<template>

  <div class="content">
    <ul>
      <li v-for="item in productList |  limitBy 6" ><a :href=item.link target = "view_window"><img :src=item.src :alt=item.alt><p v-text="item.alt"></p></a></li>
  </ul>
  </div>
</template>
<script>
  export default {
    props:{
      productList:{
        type:Array,
        required:true
      }
    }
  }
</script>
<style scoped>
  .content{
    background:url("../../assets/wrap.png") no-repeat;
    background-size:100% 100%;
    width:95%;
    height:600px;
    margin-left:22px;
  }
  .content>ul{
    padding:92px 0 0 90px;
  }
  .content>ul>li{
    display: inline-block;
    font-size:20px;
    text-align: center;
  }
  .content>ul>li:hover{
    cursor:pointer;
    box-shadow: 5px 2px 3px #666;
  }
  .content>ul>li:active{
    box-shadow: 5px 2px 3px red;
  }
  .content img{
    width:180px;
    height:102px;
    margin:30px 20px;
    box-shadow: 5px 5px 5px #666;
  }
  @media (max-width:1120px){
    .content>ul>li:last-child{
      display: none;
    }
    .content>ul{
      margin-left:50px;
    }
    .content>ul>li:nth-last-child(2){
      display: none;
    }
  }
</style>



