<template>
      <div class="tv">
        <img src=".././assets/person1.png" alt="person1" class="person1">
        <img src=".././assets/person2.png" alt="person1" class="person2">
        <div class="wrap">
        <ul class="img-watch" id="imgWrap" :style="switchClass" >
          <li v-for="item in imgList"><img :src=item.src :alt=item.alt></li>
        </ul>
        </div>
        <ul class="tv_btn">
          <li v-for="item in imgList" :class= "{active:num == $index}"  @click="getCrruent($index)" @mouseover="clearCrrunet" @mouseout = "goPlay"></li>
        </ul>
      </div>
</template>
<script>
  export default{
    data(){
      return{
        imgList:[
          {
            src:'static/img/painter.jpg',
            alt:'A21敢不凡系列画家篇'

          },
          {
            src:'static/img/Whistle-2.jpg',
            alt:'Whistle'

          },
          {
            src:'static/img/ruinsGirl.jpg',
            alt:'废墟女侠'

          },
          {
            src:'static/img/darkArea.jpg',
            alt:'异度空间'
          },
          { src:'static/img/cubilose.jpg',
            alt:'燕窝广告'
          }
        ],
        num:0,
        imgTimer:0,
        switchClass:{
          'transform':'translate3d(0,0,0)',

        }
      }
    },
    ready(){
      this.goPlay()
    },
    methods:{
      goPlay(){
        clearInterval(this.imgTimer)
        this.imgTimer = setInterval(()=>{
            this.num++;
            this.num%=5;
        this.getCrruent(this.num)
          },3000)
      },
      getCrruent(index){
        this.num = index
        this.switchClass['transform'] =`translate3d(0,${-320*index}px,0)`;
      },
      clearCrrunet(){
        clearInterval(this.imgTimer)
      }

    }
  }
</script>
<style scoped>

  .person1{
    width:150px;
    height:150px;
    position:absolute;
    right:-58px;
    top:-70px;
    z-index:-100;
  }
  .person2{
    width:150px;
    height:150px;
    position:absolute;
   left:-80px;
    top:140px;
    z-index:-1;
  }
  .tv{
    width:700px;
    height:489px;
    margin:0 auto;
    background:url("../assets/tv.png") no-repeat;
    background-size:100% 100%;
    position:relative;
  }
  .tv_btn>li{
    width:15px;
    height:15px;
    border-radius:50%;
    background:black;
    border:3px solid black;
    margin-top:15px;
  }
  .tv_btn>li:hover{
    background:white;
    cursor:pointer;
  }
  .tv_btn>li.active{
    background:white;
  }
  .tv_btn{
    margin-left:610px;
   padding-top:145px;
  }
  .wrap{
    overflow: hidden;
    width: 500px;
    height:300px;
    left:30px;
    top:130px;
    position:absolute;
  }
  .img-watch{
    position:absolute;
    left:0;
    top:0;
    z-index: -1;
  transition:all 2s;
  }
  .img-watch>li{
    height:320px;
  }
  .img-watch img{
    width: 500px;
    height:320px;
  }
</style>



