<template>

<div class="works" id="scrollCont">
  <ul class="works_one"  v-if="listSlide" transition="works_one">
    <li v-for="item in imgList | limitBy 6" ><a :href=item.link target = "view_window"><img :src=item.src :alt=item.alt><p v-text="item.alt"></p></a></li>
  </ul>
  <ul class="works_two" v-if="!listSlide"  transition="works_two">
    <li v-for="item in imgList | limitBy 5 6"><a :href=item.link target = "view_window"><img :src=item.src :alt=item.alt><p v-text="item.alt"></p></a></li>
  </ul>
  </div>
    <works-slider :list-num = "listNum" @current ="show"></works-slider>
  <img src="../../assets/person4.png" alt="person4" class="works-person4">
</template>
<script>
  import worksSlider from './worksSlider.vue'
  export default {
    props:{
      imgList:{
        type:Array,
        required:true
      }
    },
    data(){
      return{
        listNum:0, 
        currentShow:0,
        listSlide:true
      }
    },
    computed:{
        listNum:function(){
         return Math.ceil(this.imgList.length/8);//除以每页显示的图片数量，计算分页数
        }
    },
    methods:{
      show(msg){
        this.currentShow = msg
        if(msg==1){
            this.listSlide = true;
        }else if(msg ==2){      
          this.listSlide = false;
        }
      }
    },
    components:{
      worksSlider
    }
  }
</script>
<style>

  .works{
    width: 80%;
    height:850px;
    overflow:hidden;
    display: inline-block;
    margin-left:70px;
    margin-top:40px;
    position: relative;
  }
  @media (max-width:1050px){
    .works{
      width: 93%;
    }
  }

  .works>ul{
    width:700px;
    height:100%;
    position: absolute;
    top:0;
    left:0;
  }
  .works ul>li{
    display: inline-block;
    margin:40px;
    text-align: center;
    font-size:20px;
  }
  .works li img{
    width:250px;
    height:152px;
    margin-bottom: 20px;
    cursor:pointer;
  }
  .works li img:hover{
    box-shadow: 2px 3px 2px black;
  }
  .works-person4{
    width:123px;
    float:right;
  }
.works_two-transition{
  transition: all 0.3s linear;
}
.works_two-enter,.works_two-leave{
  padding-left:300px;
  opacity: 0;
}
.works_one-transition{
  transition: all 0.1s linear;
}
.works_one-enter,.works_one-leave{
   padding-left:300px;
 opacity: 0;
}

</style>



