<template>
  <ul class="pageBtn">
    <li v-for="item in listNum" :class="{active:num==$index}" @click="getCurrent($index)">{{$index+1}}</li>
  </ul>
</template>
<script>
  export default{
    props:{
      listNum:{
        type:Number,
        required:true
      }
    },
    data(){
      return{
        num:0
      }
    },
    methods:{
      getCurrent(index){
        if(this.num != index){
          this.num=index
          this.$dispatch('current',index+1)
        }

      },
    }
  }
</script>
<style scoped>
  .pageBtn{
    text-align: center;
  }
  .pageBtn li{
    display: inline-block;
    width:20px;
    height:20px;
    color:white;
    background-color:black;
    font-size:10px;
    font-family: "Arial Black";
    margin-right: 10px;
    cursor:pointer;
    line-height:20px;
  }
  .pageBtn li.active{
    background:none;
    color:black;
    cursor:auto;
  }

</style>



