<template>
    <ul class="teamList">
        <li v-for = "item in teamList">
            <img :src=item.src :alt=item.alt><ul>
                <li>{{item.alt}}</li>
                <li>{{item.manattr}}</li>
            </ul>
        </li>
    </ul>
</template>
<script>
export default{
    data(){
        return{
            teamList:[
        {
            src:"static/team/1.png",
                    alt:'林伟杰',
                manattr:"VR短片导演 特效剪辑师 "
        },
        {
            src:"static/team/2.png",
                    alt:'吕翠翠',
                manattr:"编剧 演员 监制 艺术指导"
        },
        {
            src:"static/team/3.png",
                    alt:'严杰涛',
                manattr:"导演 监制"
        },
        {
            src:"static/team/4.png",
                    alt:'伍文活',
                manattr:"创意总监 摄影师"
        },
        {
            src:"static/team/5.png",
                    alt:'陈泽斌',
                manattr:"VR编剧 无人机飞手"
        },
        {
            src:"static/team/6.png",
                    alt:'张锦钊',
                manattr:"编剧 演员 动作指导"
        },
            ]
        }
    }
}
</script>
<style>
.teamList{
    width:900px;
    margin:0 auto;
    margin-top:50px;
}
.teamList>li{
    display: inline-block;
    width:400px;
    height:200px;
    overflow: hidden;
    margin:20px;
    background-image: linear-gradient(left top,#201f1f,white);
}
.teamList img{
    width:200px;
    height:200px;
}
.teamList>li>ul{
    display: inline-block;
    vertical-align: top;
    text-align: center;
    width: 180px;
    height:100%;

    line-height: 50px;
    font-weight: bold;
    padding-top: 50px;
}
.teamList>li:hover{
    background: linear-gradient(left top,white,#201f1f);
}
.teamList>li>ul>li:first-child{
    font-size:40px;
}
.teamList>li:nth-child(3)>img,.teamList>li:nth-child(4)>img{
    float: right;
}
</style>



