import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import home from './components/home'
import product from './components/home/productView.vue'//productView属于home子组件，且为work和product的父组件
import about from './components/about/about.vue'
import contact from './components/contact/contact.vue'

Vue.use(VueRouter)
Vue.use(VueResource)

Vue.config.debug = true


const router = new VueRouter({
  hashbang:false
})




router.beforeEach(()=>{
  window.scrollTo(0,0)
})
router.map({
  '/home':{
    component: home
    },
  '/works':{
    component: product
  },
  '/about':{
    component:about
  },
  '/contact':{
    component:contact
  }
})
router.redirect({
  '*':'/home'
})
router.start(App,'app')



